/* Estilos do Menu */
.App {
    font-family: Arial, sans-serif;
  }
  
  header {
    position: relative;
  }
  
  /* Botão do Menu */
  .menu-btn {
    position: absolute;
    top: 20px;
    left: 20px;
    background: none;
    border: none;
    cursor: pointer;
    z-index: 10;
  }
  
  .menu-icon {
    width: 30px;
    height: 3px;
    background-color: #fff;
    position: relative;
    transition: transform 0.3s ease-in-out;
  }
  
  .menu-icon::before,
  .menu-icon::after {
    content: '';
    width: 30px;
    height: 3px;
    background-color: #fff;
    position: absolute;
    left: 0;
    transition: transform 0.3s ease-in-out;
  }
  
  .menu-icon::before {
    top: -8px;
  }
  
  .menu-icon::after {
    top: 8px;
  }
  
  /* Quando o Menu Está Aberto */
  .menu-btn.open .menu-icon {
    transform: rotate(45deg);
  }
  
  .menu-btn.open .menu-icon::before {
    transform: rotate(90deg);
    top: 0;
  }
  
  .menu-btn.open .menu-icon::after {
    transform: rotate(90deg);
    top: 0;
  }
  
  /* Estilo do Menu */
  .menu {
    position: fixed;
    top: 0;
    right: 0;
    background-color: #333;
    height: 100%;
    width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
  }
  
  .menu.open {
    transform: translateX(0);
  }
  
  .menu-item {
    background: none;
    border: none;
    color: white;
    font-size: 18px;
    margin: 20px 0;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .menu-item:hover {
    background-color: #555;
  }
  